@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

:root {
  --main-color: rgb(32, 106, 114);
}

body {
  margin: 0;
  font-family: 'Lato';
  min-width: 360px;
}

/* HEADER */
header nav {
  margin: 0;
}

header {
  background-color: var(--main-color);
  padding: 20px;
}
header ul {
  display: flex;
  align-items: center;
  gap: 20px;
  list-style: none;
  margin: 0;
  padding: 0;
}
header nav #responsive-bars {
  display: none;
}

header u li {
  margin: 0;
}
header ul li a {
  color: rgb(255, 255, 255, 0.75);
  text-decoration: none;
  font-size: 20px;
  font-weight: 600;
}
header ul li a:hover,
header ul li.selected a {
  color: rgb(255, 255, 255);
}

/* HOME - MAIN */
main .img-container {
  background-image: url('../img/portada-Ryan.jpeg');
  background-position: center top;
  background-size: 100% auto;
  background-repeat: no-repeat;
  width: 100%;
  min-width: 360px;
  height: 45vw;
  max-height: 450px;
  margin-top: 20px;
  text-shadow: 0px 1px 4px #080808;
  color: white;
}
main .img-container > * {
  margin: 0;
  text-align: center;
}
main .img-container h1 {
  font-size: 120px;
  padding-top: 60px;
}
main .img-container p {
  font-size: 50px;
}

/* PRESENTATION - MAIN */
main .presentation{
  max-width: 1024px;
  padding: 20px 30px;
  margin: 0 auto;
}
main .presentation .img{
  display: flex;
  gap: 10px;
}
main .presentation .img img{
  object-fit: contain;
  object-position: top;
  top: 0;
  max-width: 512px;
  margin: 0 auto;
}

main .presentation p{
  font-size: 18px;
  text-align: justify;
}
main .presentation .bottom-images{
  display: flex;
  justify-content: space-between;
  gap: 30px;
}
main .presentation .bottom-images img{
  width: 30%;
  object-fit: contain;
}

/* CATEGORY - MAIN */
main .category-tabs {
  border-bottom: 2px solid var(--main-color);
  max-width: 1024px;
  margin: 20px auto 0;
}
main .category-tabs > div {
  display: flex;
  justify-content: space-evenly;
  box-sizing: border-box;
}
main .category-tabs > div > div {
  border-top: 2px solid var(--main-color);
  border-right: 2px solid var(--main-color);
  border-left: 2px solid var(--main-color);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 15px;
  font-size: 20px;
  font-weight: 800;
  color: var(--main-color);
  box-sizing: border-box;
  cursor: pointer;
  width: 200px;
  text-align: center;
}
main .category-tabs > div > div.selected {
  background-color: var(--main-color);
  color: white;
}
main .category-container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f2f2f2;
  box-sizing: border-box;
}
main .category-container h1 {
  margin-top: 0;
  font-size: 30px;
}
main .category-container section {
  display: flex;
  background-color: white;
  margin-bottom: 20px;
  max-height: 200px;
}
main .category-container ul {
  line-height: 30px;
}
main .category-container section img {
  width: 200px;
  height: 200px;
}
main .category-container section > div {
  display: flex;
  width: 100%;
}
main .category-container section .category-middle {
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  min-width: 300px;
}
main .category-container section .category-middle .category-name {
  font-size: 24px;
  font-weight: 900;
  margin: 0
}
main .category-container section .separator {
  padding: 20px 0;
}
main .category-container section .separator > div {
  width: 1px;
  height: 100%;
  background-color: rgb(187, 185, 185);
}
main .category-container section .category-right {
  box-sizing: border-box;
  padding: 20px;
  min-width: 200px;
  text-align: center;
}
main .category-container section .category-right .category-info {
  font-size: 20px;
  margin: 20px 0 15px;
}
main .category-container section .category-right .category-btn {
  display: flex;
  justify-content: center;
}
main .category-container section .category-right .category-btn a {
  background-color: rgb(133, 196, 184);
  border-radius: 12px;
  padding: 10px 30px;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  color: black;
}
main .category-container section .category-right .category-btn a:hover {
  filter: brightness(107%);
}

/* FOOTER */
footer {
  font-family: 'Open Sans';
  justify-content: center;
  text-align: center;
  margin-top: 20px;
  color: rgb(49, 49, 49);
}
footer .info {
  padding: 20px 0;
  font-size: 18px;
  font-style: italic;
  background-color: rgb(143, 194, 199);
}
footer #cbox a {
  color: rgb(32, 106, 114);
}
footer #cbox a:hover {
  color: rgb(143, 194, 199);
}
footer form {
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
}
footer nav ul {
  display: flex;
  width: fit-content;
  gap: 20px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
footer nav ul li {
  padding: 3px;
  font-size: 18px;
  display: inline-block;
}
footer nav a {
  text-decoration: none;
  color: rgb(32, 106, 114);
}
footer a:hover {
  color: rgb(143, 194, 199);
}

#email {
  background-color: rgb(255, 255, 255);
  color: rgb(49, 49, 49);
  border: none;
  font-size: 16px;
  border-bottom: solid 1px rgb(32, 106, 114);
  text-align: center;
  padding: 0.5rem;
  margin: 1rem auto;
  max-width: 500px;
}
#email::placeholder {
  color: rgb(49, 49, 49);
  font-size: 16px;
}
#email:focus::placeholder {
  color: transparent;
}
#cbox {
  font-size: 14px;
  padding-bottom: 2rem;
}
#button {
  color: rgb(0, 0, 0);
  background-color: rgb(143, 194, 199);
  width: 150px;
  padding: 0.5rem;
  border: none;
  margin: 0 auto;
  font-size: 18px;
  border-radius: 10px;
  cursor: pointer;
}
#button:hover {
  filter: brightness(107%);
}
form a {
  text-decoration: none;
}
.copyright {
  font-size: 10px;
  padding: 0.5rem;
}
.copyright p {
  padding-bottom: 0.5rem;
}

/* Mobile and Tablet (portrait mode)*/
@media screen and (max-width: 1024px) {
  main .img-container h1 {
    font-size: 12.5vw;
    padding-top: 60px;
  }
  main .img-container p {
    font-size: 4.5vw;
  }
  main .presentation h1 {
    font-size: 18px;
  }
  main .presentation h2,
  main .presentation p {
    font-size: 16px;
    text-align: justify;
  }
  main .presentation .bottom-images {
    flex-direction: column;
    gap: 30px;
  }
  main .presentation .bottom-images img {
    width: 50%;
    margin: 0 auto;
  }
}
@media screen and (max-width: 870px) {
  main .category-container section {
    flex-direction: column;
    max-height: unset;
  }
  main .category-container section img {
    margin: 0 auto;
  }
}
@media screen and (max-width: 768px) {
  main .presentation .img {
    flex-direction: column;
  }
  main .presentation .img img {
    width: 100%;
    margin-bottom: 15px;
    padding: 0 10px;
    box-sizing: border-box;
  }
  main .presentation .bottom-images img {
    width: 80%;
  }
}
@media screen and (max-width: 560px) {
  main .category-container h1 {
    font-size: 26px;
  }
  main .category-container section > div {
    flex-direction: column;
  }
  main .category-container section .separator {
    padding: 0 20px;
  }
  main .category-container section .separator > div {
    width: 100%;
    height: 1px;
  }
  main .category-container section .category-right .category-info {
    margin: 0 0 15px;
  }
}
@media screen and (max-width: 500px) {
  header {
    padding: 0;
  }
  header nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  header nav #responsive-bars {
    display: flex;
    align-items: center;
    padding: 10px;
    color: white;
    margin-left: 10px;
    font-size: 26px;
    cursor: pointer;
  }
  header nav ul {
    flex-direction: column;
    gap: 0;
    width: 100%;
  }
  header nav ul li {
    padding: 10px;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    margin: 0;
  }
  header nav ul li a {
    font-size: 26px;
  }
  header nav ul li.responsive-hidden {
    display: none;
    padding: 5px;
    width: 100%;
    background-color: darkgrey;
    text-align: center;
    padding: 15px;
  }
  header nav ul li:not(:first-child) {
    border-top: 1px solid white;
  }
  header ul li:first-child a {
    color: rgb(255, 255, 255);
  }
  header ul li.selected {
    order: 0;
  }

  /* MAIN */
  main .img-container {
    height: 200px;
  }

  main .img-container h1 {
    font-size: 50px;
  }
  main .img-container p {
    font-size: 20px;
  }
  main .category-tabs > div > div {
    width: 150px;
    font-size: 18px;
    padding: 10px 15px;
  }
  main article section .recommended > div > div {
    font-size: 18px;
  }
  footer nav ul {
    flex-direction: column;
    gap: 10px;
  }
}
